import React, { useEffect, useRef } from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import RecommendSection from "../components/Store/RecommendSection";
import * as s from "./style/StoreStyle";
import * as r from "../components/style/RecommendSectionStyle";
import { Data } from "../Data/Store_Main1";
import { Data2 } from "../Data/Store_Main2";
import StoreDetail from "../components/Store/StoreDetail";
import StoreDetail2 from "../components/Store/StoreDetail_2";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import PullToRefresh from "../common/PullToRefresh";
import { sleep } from "../utill";
import { useAppDispatch } from "../useRedux/rootReducer";

const category = [
    { title: "", img: "/img/Rectangle 2959.png" },
    { title: "", img: "/img/Rectangle 2960.png" },
    { title: "", img: "/img/Rectangle 2961.png" },
    { title: "", img: "/img/Rectangle 2962.png" },
    { title: "", img: "/img/Rectangle 2963.png" },
    { title: "", img: "/img/Rectangle 2964.png" },
    { title: "", img: "/img/Rectangle 2965.png" },
    { title: "", img: "/img/Rectangle 2966.png" }
];

const BaseURL = "https://paytalk.github.io/Paytalk_webview";

const StoreContainer = () => {
    const [detail2, setDetail2] = useState(false);
    const [detail3, setDetail3] = useState(false);
    const navigate = useNavigate();
    const [scrollY, setScrollY] = useState(0);
    const dispath = useAppDispatch();
    const handleScroll = () => {
        const scrollPosition = window.pageYOffset;
        setScrollY(scrollPosition);
    };

    useEffect(() => {
        window.addEventListener("scroll", handleScroll);

        return () => {
            window.removeEventListener("scroll", handleScroll);
        };
    }, []);

    const mutate = async () => {
        await sleep(2000);
    };

    return (
        <>
            <PullToRefresh onRefresh={mutate}>
                <s.StoreContainerWrap>
                    <s.StoreContainerArea>
                        <s.StoreContainerTitle
                            onClick={() => console.log(scrollY)}
                        >
                            
                        </s.StoreContainerTitle>
                        <s.StoreContainerCategory>
                            
                        </s.StoreContainerCategory>
                        <s.StoreContainerThe></s.StoreContainerThe>
                        <s.StoreCategoryBox>
                            <s.StoreCategoryUl>
                                {category.map((index, key) => (
                                    <s.StoreCategoryLi
                                        onClick={() => {
                                            setTimeout(
                                                () =>
                                                    navigate(
                                                        `/store/detail/${key}`
                                                    ),
                                                10
                                            );
                                            dispath({
                                                type: "urlDepth",
                                                payload: "next"
                                            });
                                        }}
                                    >
                                        <s.StoreCategoryImg>
                                            <img
                                                src={`${BaseURL}` + index.img}
                                                alt=" "
                                                key={key}
                                            />
                                        </s.StoreCategoryImg>
                                        <s.StoreCategoryTitle key="title">
                                            {index.title}
                                        </s.StoreCategoryTitle>
                                    </s.StoreCategoryLi>
                                ))}
                            </s.StoreCategoryUl>
                        </s.StoreCategoryBox>
                        <s.Gubun></s.Gubun>
                        {/* <RecommendSection
            title={Data.data.market1_title}
            data={Data.data.product}
            ></RecommendSection> */}
                        <r.StoreRecommendBox>
                            <r.Title onClick={() => setDetail2(true)}>
                                <p>{Data.data.market1_title}</p>
                                <span></span>
                            </r.Title>
                            <r.StoreRecommendItemBox>
                                <r.StoreRecommendUl>
                                    {Data.data.product.map(index => (
                                        <r.StoreRecommendLi>
                                            <r.StoreRecommendImg>
                                                <img
                                                    src={
                                                        `${BaseURL}` +
                                                        index.image
                                                    }
                                                    alt=""
                                                />
                                            </r.StoreRecommendImg>
                                            <r.StoreRecommendTitle>
                                                {index.name}
                                            </r.StoreRecommendTitle>
                                            <r.StoreRecommendCost>
                                                {index.price}
                                            </r.StoreRecommendCost>
                                        </r.StoreRecommendLi>
                                    ))}
                                </r.StoreRecommendUl>
                            </r.StoreRecommendItemBox>
                        </r.StoreRecommendBox>
                        <s.Gubun></s.Gubun>
                        {/* <RecommendSection
            title={Data2.data.market2_title}
            data={Data2.data.product}
            ></RecommendSection> */}
                        <r.StoreRecommendBox>
                            <r.Title onClick={() => setDetail3(true)}>
                                <p>{Data2.data.market2_title}</p>
                                <span></span>
                            </r.Title>
                            <r.StoreRecommendItemBox>
                                <r.StoreRecommendUl>
                                    {Data2.data.product.map(index => (
                                        <r.StoreRecommendLi>
                                            <r.StoreRecommendImg>
                                                <img
                                                    src={
                                                        `${BaseURL}` +
                                                        index.image
                                                    }
                                                    alt=""
                                                />
                                            </r.StoreRecommendImg>
                                            <r.StoreRecommendTitle>
                                                {index.name}
                                            </r.StoreRecommendTitle>
                                            <r.StoreRecommendCost>
                                                {index.price}
                                            </r.StoreRecommendCost>
                                        </r.StoreRecommendLi>
                                    ))}
                                </r.StoreRecommendUl>
                            </r.StoreRecommendItemBox>
                        </r.StoreRecommendBox>
                    </s.StoreContainerArea>
                </s.StoreContainerWrap>
            </PullToRefresh>
        </>
    );
};

export default StoreContainer;
